import React from 'react';
//style
import './aboutMe.css';

function AboutMe() {
    return (
        <div className="about-me">
            <h1>Mam na imi Justyna</h1>
            <p>
                Jestem Mam Jasia i on Konrada.
                Mam w yciu to szczcie, e otaczaj mnie fajni ludzie z duym poczuciem humoru i sporym dystansem do siebie - co pozwala mi bezkarnie <span role="img" aria-label=""></span> ujmowa ich w codziennych zmaganiach z yciem i przeciwnociami losu, w moich rysunkach.
                Znajdziecie tu gwnie sceny z mojego wasnego ycia, z Konradem w roli gwnej, moich przyjaci a take kolegw z pracy i komizm sytuacyjny z nimi zwizany. Czyli mwic wprost - moj codzienno przelan na kartk <span role="img" aria-label=""></span>
                Znajdziecie tu take rysunki zespow z ktrymi w jaki sposb mentalnie czuj si zwizana, rysunki postaci, zwierzt, ilustracje do bajek itd.
                Narzdziem mojej pracy jest gwnie pisak CD i kartka vs Komputer i paint <span role="img" aria-label=""></span>
                Nie s to Twory doskonae, rysowane s szybko w przypywie emocji, gwnie tych pozytywnych i dlatego s wane.
                Jeli masz ochot zosta uwieczniony na moim rysunku, moj kresk (specyficzny brak nosa i dua szpara midzy zbami<span role="img" aria-label=""></span>) wylij maila ze zdjciem i scharakteryzuj si (kilka sw o sobie, co lubisz, jaki/jaka jeste).
                W miar moliwoci postaram si Ciebie uwieczni <span role="img" aria-label=""></span> Serdecznie zapraszam.
            </p>
        </div>
    );
}

export default AboutMe;